'use client';
import { useState } from "react";
import { onBurgerClick } from "../Header/header";
export default function BurgerMenu() {
    const [open, setOpen] = useState(false);
    return (
        <button onClick={e => onBurgerClick(e.target, open)} className="header__burger js-burger" aria-label="menu">
            <span className="icon"></span>
        </button>
    );
}